        pdf.setFontSize(9);
        pdf.text('Sistema de Gesti√≥n de Permisos de Trabajo', pageWidth / 2, 19, { align: 'center' });

        // Informaci√≥n del permiso a la derecha
        pdf.setFont('helvetica', 'bold');
        pdf.setFontSize(11);
        pdf.text(`No. ${permit?.numeroPermiso || 'SIN ASIGNAR'}`, pageWidth - margin, 12, { align: 'right' });
        
        // Estado con color
        const estado = permit?.status?.toUpperCase() || 'BORRADOR';
        pdf.setFont('helvetica', 'normal');
        pdf.setFontSize(8);
        
